import React, { Component } from 'react';
import styled from "styled-components";
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Typography from '@material-ui/core/Typography';
import Checkbox from '@material-ui/core/Checkbox';
import Favorite from '@material-ui/icons/Favorite';
import FavoriteBorder from '@material-ui/icons/FavoriteBorder';
import Chip from '@material-ui/core/Chip';

const TitleFont = styled.div`   
    font-size: 18px;
    color: black;
    font-family: 'Play', sans-serif;
`;

const GuideMentFont = styled.div`
  font-size: 15px;
  color: #747474;
  font-family: 'Noto Sans KR', sans-serif;
  line-height: 25px;
`;

const ChipLocation = styled.div`
  margin-left: 17px;
`;

const ImageEvent = styled.div`
  cursor:pointer;
  overflow:hidden;
  transform:scale(1); 
  transition:transform 0.2s linear;
  &:hover {
    filter: brightness(80%);  
    transform: scale(1.1);
  } 
`;


const styles = theme => ({
    loginCard: {
      height: 200,
      width: 300
    }
  });

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 300,
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  }
}));


export default function RecipeReviewCard(props) {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };


  return (
    <div>
    <Card className={classes.root}>

        <ImageEvent>
        <a href = {props.url} target="_blank">
        <CardMedia
            className={classes.media}
            image={props.image}
        />
        </a>
</ImageEvent>
        <CardContent>
            
                <TitleFont>{props.title}</TitleFont>
                <GuideMentFont>
                <br></br>
                {props.guideMent}
                {props.br}
                {props.br}
                <br></br>
                </GuideMentFont>
        </CardContent>
        
        <ChipLocation><Chip label={props.chip1} />&nbsp;
        <Chip label={props.chip2} />&nbsp;
        <Chip label={props.chip3} /></ChipLocation>

        <CardActions disableSpacing>
            {<Checkbox icon={<FavoriteBorder />} checkedIcon={<Favorite />} name="checkedH" />}
        </CardActions>

        <Collapse in={expanded} timeout="auto" unmountOnExit>
            <CardContent>
                <Typography paragraph>
                  {props.hiddenMent}
                </Typography>

                <Typography paragraph>
                </Typography>
            </CardContent>
      </Collapse>
    </Card>
    </div>
  );
}

