import React, { Component } from 'react';
import styled from "styled-components";

const FloorStyle = styled.div`
    width: 100%;
    height: 100px;
    background-color: #ccffff;
    @media (min-width:0px) and (max-width:680.9px) {
       bottom: -100; width: 100%;
    }
`;

const FontLocation = styled.div`
    padding-top: 50px; 
    margin-left: 70%;
    @media (min-width:0px) and (max-width:680.9px) {
        margin-left: 0%;
        text-align: center;
    }
`;

const FontStyle = styled.div`
    font-size: 16px;
    color: #4c4c4c;
    @media (min-width:0px) and (max-width:680.9px) {
        font-size: 13px;
    }
`;

class Floor extends Component {
    render() {
        return (
        <div>
            <FloorStyle>
                <FontLocation>
                    <FontStyle>
                    <b>이 웹사이트는 개인 포트폴리오용으로 제작되었습니다.</b>
                    </FontStyle>
                </FontLocation>
            </FloorStyle>
        </div>
        );
    }
}

export default Floor;