import React, { Component } from 'react';
import styled from "styled-components";

const AssistMentStyle = styled.div`
    font-size: 18px;
    font-family: 'Noto Sans KR', sans-serif;
    color: #747474;
    line-height: 30px;
    margin-bottom: 30px;

    
    @media (min-width:0px) and (max-width: 1023.9px) {
        font-size: 12.5px;
    }
`;

const AssistTitleStyle = styled.div `
    width: 100%;
    height: 100%;
    color: #black;
    font-size: 25px;
    text-decoration: none;
    transition: 1s;
    font-family: 'Do Hyeon', sans-serif;

    @media (min-width:0px) and (max-width: 680.9px) {
        font-size: 19px;
    }
`;


class MentBox extends Component {

    render() {
        return (
        <div>
            <AssistTitleStyle>
                <p>포트폴리오를 끝까지 읽어주셔서 감사합니다.</p>
            </AssistTitleStyle>
            
            <AssistMentStyle>
                <p>언제든 편하게 연락주시기 바랍니다!<br /></p>
            </AssistMentStyle>

            
        </div>
        );
    }
}

export default MentBox;        