import React, { Component } from 'react';
import styled from "styled-components";

const FontStyle = styled.div`
    color: #5d5d5d;
    font-size: 70px;
`
const ClockShadow = styled.div`
    width: 280px;
    height : 20px;
    background-color: #dddddd;
    margin-top: -25px;
`

class Clockcmp extends Component {

    constructor(props){
        super(props)
        this.state = {d: new Date()}
    }

    componentDidMount() { // Clockcmp 컴포넌트가 불러올때마다 1초씩 this.Change()를 부른다 
        this.timeID = setInterval(
            () => this.Change(),
            1000
        )
    }

    componentWillUnmount(){ //종료되면 반복하는것도 클리어시키기
        clearInterval(this.timeID)
    }


    Change = () => {  //시계 구현

        this.setState({
            d : new Date(),
        })
    }
    

    render() {
        return(
            <div onload="printClock()">
                <FontStyle>
                   <b>{this.state.d.getHours() < 10 ? "0" + this.state.d.getHours() : this.state.d.getHours()}:{this.state.d.getMinutes() < 10 ? "0" + this.state.d.getMinutes() : this.state.d.getMinutes()}:{this.state.d.getSeconds() < 10 ? "0" + this.state.d.getSeconds() : this.state.d.getSeconds()}</b>
                </FontStyle>
                <ClockShadow></ClockShadow>
            </div>)
    }
}

export default Clockcmp;