import React, { Component } from 'react';
import styled from "styled-components";

const ButtonStyle = styled.div `
    height: 80px;
    float: left;
    color: #127da7;
    cursor: pointer;
    font-family: 'Nunito', sans-serif;
    transition: all 0.3s;
    font-size: 15px;

    &:hover {
        transform: scale(1.1);
        background-color: #b2ebf4;
        opacity: 0.8;
    }

    &:active {
        transform: scale(1.1);
        background-color: #aaaaaa;
    }

    /*해상도*/
    @media (min-width:0px) and (max-width: 767.9px) {
        width: 80px;
        text-align: center;
        font-size: 14px;
    }

    @media (min-width:768px) and (max-width: 1023.9px) {
        width: 100PX;
        text-align: center;
        font-size: 14px;
    }
    @media (min-width:1024px) {
        width: 100PX;
        text-align: center;
        font-size: 16px;
        
    }

`;


class MenuButton extends Component {
    scrollToAboutme = (event) => {
        window.scroll({top:850, left:0, behavior:'auto'});
    };

    scrollToSkills = (event) => {
        window.scrollTo({top:2700, left:0, behavior:'auto'});
    };

    scrollToProject = (event) => {
        window.scrollTo({top:3500, left:0, behavior:'auto'});
    };

    scrollToContact = (event) => {
        window.scrollTo({top:5000, left:0, behavior:'auto'});
    };
    render() {
        return ( 
        <div>
            <a onClick={this.scrollToAboutme}>
                <ButtonStyle>   
                    <span style={{textAlign: "center", lineHeight: "80px" }}>ABOUTME</span>
                </ButtonStyle>
            </a>
            <a onClick={this.scrollToSkills}>
                <ButtonStyle>   
                    <span style={{textAlign: "center", lineHeight: "80px" }}>SKILLs</span>
                </ButtonStyle>
            </a>
            <a onClick={this.scrollToProject}>
                <ButtonStyle>   
                    <span style={{textAlign: "center", lineHeight: "80px" }}>PROJECT</span>
                </ButtonStyle>
            </a>
            <a onClick={this.scrollToContact}>
                <ButtonStyle>   
                    <span style={{textAlign: "center", lineHeight: "80px" }}>CONTACT</span>
                </ButtonStyle>
            </a>
        </div>
        );
    }
}

export default MenuButton;