import React, { Component } from 'react';
import styled from "styled-components";

const AssistTitleStyle = styled.div `
    width: 100%;
    height: 100%;
    color: #black;
    font-size: 25px;
    text-decoration: none;
    transition: 1s;
    font-family: 'Do Hyeon', sans-serif;
    @media (min-width:0px) and (max-width:680.9px) {
        font-size: 23px;
    }
`;

const AssistMentStyle = styled.div`
    font-size: 15px;
    font-family: 'Noto Sans KR', sans-serif;
    color: #747474;
    line-height: 30px;

    @media (min-width:0px) and (max-width:320.9px) {
        font-size: 10.5px;
    }

    @media (min-width:321px) and (max-width: 1023.9px) {
        font-size: 11.5px;
    }
`;

class AssistBox extends Component {  
    render() {
        return (
        <div>
            <AssistTitleStyle>
                
                <p>
                    <b>덕업일치</b><br /><br />
                    " 취미에서 꿈으로 꿈에서 현실로 !! "
                </p>
                
            </AssistTitleStyle>
            <AssistMentStyle>
                <p>
                    고구마S 카페를 통해 프로그래밍을 접하였고<br />
                    무작정 서점으로 달려가<br />
                    C언어 책을 구매해 프로그래밍을 시작했습니다.<br />
                    철부지 중학생 시절 저에게 프로그래밍이란<br />
                    게임처럼 재밌는 취미였습니다.<br />  
                    
                </p>
            </AssistMentStyle><br></br>
            <AssistMentStyle>
                <p>
                    하지만 여러 사정이 겹쳐 고등학교를 자동차과로 진학하게 되었고<br />
                    취업을 위해 고등학교 생활에 집중하던 탓에<br />
                    프로그래밍이란 취미는 자연스럽게 멀어지는 듯 싶었습니다.<br />
                </p>
            </AssistMentStyle><br></br>
            <AssistMentStyle>
                <p>
                    미성숙하고 준비되지 않았지만<br />
                    결국 취업이란 갈림길에 서게 된 고등학교 3학년 시절<br />
                    자동차란 분야는 제 적성에 맞지 않았고<br />
                    그것이 제 인생의 터닝포인트가 되었습니다.<br />
                </p>
            </AssistMentStyle><br></br>
            <AssistMentStyle>
                <p>
                    적성에 맞지 않는 일을 하며 살아간다는 것은<br />
                    그냥 살기 위해 일을 하는 것이구나 라는 생각이 들었습니다.<br />
                </p>
            </AssistMentStyle><br></br>
            <AssistMentStyle>
                <p>
                    과거 프로그래밍을 하며 진정으로 행복을 느끼는 저 자신이 떠올랐고<br />
                    취미를 꿈으로 만들어 실현할 수 있다는 생각에<br />
                    개발자라는 꿈이 생겼습니다.
                </p>
            </AssistMentStyle>
        </div>
        )
    }
}

export default AssistBox;