import React, { Component } from 'react';
import styled from "styled-components";
import HomeMent from './HomeMent.js';
import tmp from '../Home/tmp.jpg';

const HomeStyle = styled.div `
    width: 100%;   
    height: 100vh;
    margin-top: -43px;
    position: relative;
    text-align: center;
    background-image: linear-gradient( rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),url(${tmp});
    background-attachment: fixed;
    background-size: cover;   
    overflow: hidden;
    z-index:1;
`;

const MentLocation = styled.div`
    width: 100%;
    height: 100%;

    /*해상도*/
    @media screen and (min-width: 0px) and (max-width: 374.9px) {
    padding-top: 30vh;
    font-size: 40px;
    }

    @media screen and (min-width: 375px) and (max-width: 424.9px) {
        padding-top: 30vh;
        font-size: 46px;
    }

    @media screen and (min-width: 425px) and (max-width: 767.9px) {
        padding-top: 30vh;
        font-size: 46px;
    }

    @media screen and (min-width: 768px) and (max-width: 1439.9px) {
        padding-top: 40vh;
        font-size: 50px;
    }

    @media screen and (min-width: 1440px) {
        padding-top: 40vh;
        font-size: 60px;
    }
`;

class HomeBox extends Component {
    render() {
        return (
        <div> 
            <HomeStyle>
                <MentLocation>
                    <HomeMent name = {"Shin Jun Seop"} subject={"S/W DEVELOPER"} introduction={"방문해 주셔서 감사합니다."}></HomeMent>
                </MentLocation>
            </HomeStyle>
        </div>
        );
    }
}

export default HomeBox;