import React, { Component } from 'react';
import styled, {keyframes} from 'styled-components';

const Name = styled.div`
    width: 100%;
    height: 100%;
    color: white;
    font-family: 'DM Sans', sans-serif;
    margin-top: -8%;
    font-size: 33px;
`

const Subject = styled.div `
    width: 100%;
    height: 100%;
    color: #CCFFFF;
    font-family: 'Catamaran', sans-serif;
`;

const Introduction = styled.div `
    width: 100%;
    height: 100%;
    padding-top: 2.5%;
    color: white;
    /*해상도*/
    @media screen and (min-width: 0px) and (max-width:767.9px) {
        font-size: 20px;
    }

    @media screen and (min-width: 768px) and (max-width: 1439.9px) {
        font-size: 25px;
    }

    @media screen and (min-width: 1440px) {
        font-size: 30px;
    }
`;

const Contact = styled.div`
    position: absolute;
    width: 260px;
    height: 60px;
    background-color: black;
    left: 50%;
    margin-left: -130px;
    margin-top: 30px;
    background-color: rgba( 153, 153, 153, 0.3);
    transition: all 0.3s;
    border-radius: 5px;
    &:hover {
        transform: scale(1.01);
        background-color: #aaaaaa;
    }

    &:active {
        transform: scale(1.1);
        background-color: black;
    }

    @media screen and (max-width: 1440.9px) {
        width: 200px;
        height: 45px;
        margin-left: -100px;
    }
`;

const ContactFontStyle = styled.div`
    font-size: 40px;
    color: #5c5c5c;
    cursor:pointer;

    @media screen and (max-width: 1440.9px) {
        font-size: 32px;
    }
`

const Mouse = styled.div`
    position: absolute;
    width: 30px;
    height: 50px;
    border: 5px solid #ffffff;
    border-radius: 20px;
    left: 50%;
    top: 85%;
    margin-left: -20px;
`;

const Amouse = keyframes`
0%{
    top:5px;
    opacity: 1;
}
100% {
    top: 20px;
    opacity: 0;
    }
`;

const MouseAfter = styled.div`
    content: '';
    position: absolute;
    width: 8px;
    height: 8px;
    background: #ffffff;
    top: 8px;
    left: 50%;
    matgin-top: -10%;
    margin-left: -4px;
    border-radius: 50%;
    animation: ${Amouse} 2s infinite;
    opacity: 1;
`;


class HomeMent extends Component {
    scrollToTop = (event) => {
        window.scrollTo({top:10000, left:0, behavior:'auto'});
      };

    render() {
        return (
        <div>
            <Name><b>{this.props.name}</b> <br /> <br /><br /></Name>
            <Subject>
                {this.props.subject}
            </Subject>

            <Introduction>
                <b>{this.props.introduction}</b>
            </Introduction>
            <Contact>
                
                    <div className="top">
                        <a onClick={this.scrollToTop}>
                            <ContactFontStyle>CONTACT</ContactFontStyle>
                        </a>
                    </div>
            </Contact>
            <Mouse>
                <MouseAfter></MouseAfter>
            </Mouse>
        </div>
        );
    }
}

export default HomeMent;