import React, { Component } from 'react';
import styled from "styled-components";
import ContactTitle from './ContactTitle.js'
import Clock from './Clockcmp.js';
import MentBox from './MentBox.js';
import GitHubIcon from '@material-ui/icons/GitHub';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import KakaoQR from './img/kakaoQR.png';

const ContactStyle = styled.div`
    width: 100%;
    height: 100%;
    @media (min-width:0px) and (max-width:680.9px) {
        margin-top: 60px;
    }
    
`;

const ContactLocation = styled.div `
    height: 100%;
    /*해상도*/

    @media (min-width:0px) and (max-width:680.9px) {
        width:320px;
    
    margin:0 auto;
    }

    @media (min-width:681px) and (max-width:767.9px) {
        margin-left: 100px;
    }
    
    @media (min-width:768px) and (max-width:1023.9px) {
        margin-left: 100px;
    }

    @media (min-width: 1024px) and (max-width: 1599.9px) {
        margin-left: 307px;
    }
    
    @media (min-width: 1600px) {
        margin-left: 20.3%;
    }
`;

const Flex = styled.div`
    font-family: 'Play', sans-serif;  
    @media (min-width:0px) and (max-width:680.9px) {
        margin-top: -350px;
    }
`;

const QR = styled.div `
    width: 150px;
    height: 150px;
    background:url(${KakaoQR});
    transition: 1s;
    display: block;
    @media (min-width:0px) and (max-width:349.9px) {
        margin-left: -2px;
    }
    
`;

class Contact extends Component {
    render() {
        return (
        <div>
            <ContactLocation>
                <ContactStyle>
                    <ContactTitle></ContactTitle>
                    <MentBox></MentBox>
                    <Clock></Clock>
                    <br></br><br></br>
                    <table style={{fontSize:"18px"}}>
                    <tbody>
                        <tr>
                            <td><MailOutlineIcon style={{ fontSize: 35 }}></MailOutlineIcon></td>
                            <td><b>&nbsp;d.nautia@gmail.com</b></td>
                        </tr>
                        <tr>
                            <td><GitHubIcon style={{ fontSize: 35 }}></GitHubIcon></td>
                            <td><b>&nbsp;https://github.com/nautia</b></td>
                        </tr>
                        
                        <tr>
                        
                            <td colSpan="2">
                                <br></br>
                                <br></br>
                                <QR></QR>
                            </td>
                        </tr>
                    </tbody>
                </table>
                </ContactStyle>
                
            </ContactLocation>
            <br></br><br></br>
            
            
        </div>
        );
    }
}

export default Contact;
