import React, { Component } from 'react';
import styled from "styled-components";
import ProfileItem from './ProfileItem.js';
import tmp4 from '../AboutMe/img/gitt.png'
import tmp5 from '../AboutMe/img/gg.png'
import { CopyToClipboard } from 'react-copy-to-clipboard'
import CustomizedSnackbars from './SnackBar.js'
const ProfileStyle = styled.div `
    width: 100%;
`;


const Img = styled.div`
    width: 25px;
    height: 25px;
    background:url(${tmp4});
    margin-left: 10px;
    cursor: pointer;
`


const G = styled.div`
    width: 20px;
    height: 20px;
    margin-top:3px;
    background:url(${tmp5});
    cursor: pointer;
`;

const Flex = styled.div`
    display: flex;
`

  const url = 'd.nautia@gmail.com'; // url 복사
  
class ProfileBox extends Component {  
    profile= [{
            text:'신준섭',
            fsize: "23px",
            fweight: "bold"
        },{
            text:'S/W Devloper',
            fsize: "20px",
            fweight: "normal"
        }
    ]

    renderProfile = (arr) => {
        return arr.map(a=> {
            return <ProfileItem text={a.text} fsize={a.fsize} fweight={a.fweight} key={a.text}/>
        })
    }
    

    render() {
        return (
            
        <div>
            <article>
                <ProfileStyle>
                    {this.renderProfile(this.profile)}
                </ProfileStyle>
            </article>
            
            <Flex>
                <CopyToClipboard text={url}>
                    <a><CustomizedSnackbars></CustomizedSnackbars></a>
                </CopyToClipboard>
                
            
            
                <a href="https://github.com/nautia" target="_blank"><Img></Img></a>
                
            </Flex>
        </div>
        )
    }
}

export default ProfileBox;